import React from "react";
import CrueltyFree from "../../assets/images/cruelty_free.png";
import Edp from "../../assets/images/EDP_new.png";
import ElleBeauty from "../../assets/images/elle_beauty.png";
import FreeShipping from "../../assets/images/free_shipping.png";
import MadeInIndia from "../../assets/images/made_in_india.png";
import Oils from "../../assets/images/oil.png";
import "./index.css";

const Usp = (props) => {
  return (
    <>
      <div className={`${props.background ? 'background' : ''} uspBanner`}>
        <div className={`uspBannerFlex w-100 align-items-center flex-column justify-content-center py-1 ${props.customClass ? props.customClass : ''}`}>
          {props.title && <div className="fs-3 text-center mb-3 d-none d-lg-block">WHY EMBARK?</div>}
          <div className="w-100 align-items-center justify-content-center row">
            <div className="col-4 col-lg-2">
              <img className="uspImage" src={CrueltyFree} alt="Cruelty Free" />
            </div>
            <div className="col-4 col-lg-2">
              <img className="uspImage" src={Edp} alt="EDP" />
            </div>
            <div className="col-4 col-lg-2">
              <img className="uspImage" src={ElleBeauty} alt="Elle Beauty" />
            </div>
            <div className="col-4 col-lg-2">
              <img className="uspImage" src={FreeShipping} alt="Free Shipping" />
            </div>
            <div className="col-4 col-lg-2">
              <img className="uspImage" src={Oils} alt="Oils" />
            </div>
            <div className="col-4 col-lg-2">
              <img className="uspImage" src={MadeInIndia} alt="Made In India" />
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Usp;